	module.exports = {"data":{"markdownRemark":{"html":"<p>[[[nicovideo:videoid=sm2197976,alt=こんな初音ミクはアンインストール♪]]]</p>\n<p><a href=\"http://www.nicovideo.jp/watch/sm2197976\">こんなの作ってみたテスト。</a>ニコ動の垢持ってないけどmixiならおｋな方は<a href=\"http://video.mixi.jp/view_video.pl?owner_id=1112991&#x26;video_id=2767395\">こちらのミラーもどうぞ(やや低画質)</a></p>\n<p>しかしそろそろこのサイトもリニューアルしなきゃあなぁ。ニコ動のサムネイルを貼り付けるXMLタグを作ってて改めて思った。XMLスタイルシートの設計が今となってはズタボロのひどい泥団子状態だし。</p>\n<p>movableType突っ込んでみたがpostgreSQLの連携がうまく取れず(いくつかのperlモジュールが古いぞバーローって怒られる→CPANから持ってきても認識してくれない→use libしても変わらず)、ppBlog突っ込んでみたがカスタマイズ性でうーむって状態。XMLの便利さに慣れてしまうと(特に更新時はHTMLよりよほど便利)なかなか抜け出せないが、その分見る人に迷惑をかけてるからなぁ。(WebブラウザがXMLtoHTMLの変換作業をしている→その分読み込みが重く感じる)なのでもうXSLで見た目カスタマイズ出来るようなBlogシステムでも作ってしまうかと思ってるが、時間がｇ(←初音ミクを描いてる暇はあるらしい…ってか去年からそんなこと言ってたような気が)そんなBlogシステムって……あるわけないよね orz(ちょっと怠けてみたかった)</p>","frontmatter":{"cover":null,"date":"2008-02-02T00:00:00+09:00","strDate":"2008/2/1","title":"こんな初音ミクはアンインストール♪"}}},"pathContext":{"path":"/ja/blog/2008/02/02-こんな初音ミクはアンインストール♪/","langKey":"ja"}}

